$primary_color: #007cba;
$secondary_color: #5f5f5f;
$background_color: #dcdcde;
$white_color: #ffffff;

.fb-setting-responsive-control-wrapper {
	.fb-setting-responsive-control {
		margin-bottom: 0;
	}
}

.fb-team-member-main-wrapper {
	position: relative;
	overflow: hidden;
}

.fb-team-member-wrapper {
	.fb-team-member-item {
		.fb-team-member-image-wrapper {
			.fb-team-member-image-overlay {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				z-index: 1;
			}

			img {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}
}

/** End General scss for Advanced Button **/

.fb-feature-wrapper {
	.dnd-items-wrapper {
		list-style: none;
		padding: 0;
		margin: 0;

		li.sortable-item {
			margin-bottom: 10px;
			margin-top: 10px;

			.sortable-header {
				display: flex;
				justify-content: space-between;
				align-items: center;
				background: #f9f9f9;
				border: 1px solid #ddd;
				border-radius: 4px;

				.sortable-title {
					flex: 12;
					cursor: pointer;
					overflow: hidden;
					padding-left: 5px;
					text-overflow: ellipsis;
					white-space: nowrap;
					width: 200px;
				}

				.sortable-sort {
					border-right: 1px solid #b4b4cb;
					border-left: 1px solid #b4b4cb;
					button {
						&:focus {
							box-shadow: none;
							outline: none;
						}
					}
				}

				.sortable-delete {
					button {
						color: #d63638;
						&:focus {
							box-shadow: none;
							outline: none;
						}
					}
				}
			}

			.sortable-item-content {
				padding-top: 10px;
			}
		}
	}
}

.fb-feature-icon-group-btn {
	display: flex;
}


.fb-image-upload-field-wrapper {
	.components-base-control__field {
		position: relative;

		&:hover {
			.editor-post-featured-image__actions {
				opacity: 1;
			}
		}

		.editor-post-featured-image__actions {
			width: 100%;
			height: unset;
			padding: 8px 12px;
			text-align: center;
			justify-content: center;
			z-index: 1;

			&.active {
				background-color: $secondary_color;
				color: $primary_color;
				border-radius: 3px;
			}
		}
	}
}

button {
	&.components-button {
		&.fb-feature-icon-single-btn {
			align-items: center;
			display: flex;
			flex: 1;
			justify-content: center;
			padding: 6px;
			height: unset;
			line-height: 16px;
			box-shadow: inset 0 0 0 1px #dcdcde;
			color: #5f5f5f;
			margin-bottom: 10px;
			&.active {
				background-color: #dcdcde !important;
				color: #5f5f5f !important;
			}
		}
		&:focus {
			outline: none !important;
			box-shadow: none !important;
		}
	}
}

.editor-post-featured-image__actions {
	z-index: -1
}


.fb-team-member-main-wrapper {
	position: relative;
	overflow: hidden;
}

.fb-team-member-wrapper {
	.fb-team-member-item {
		position: relative;
		overflow: hidden;

		.fb-team-member-image-wrapper {
			.fb-team-member-image-overlay {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				z-index: 1;
			}

			img {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
			}
		}
	}
}
